nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  margin: 2rem;
}
nav ul li{
  list-style: none;   
}
nav ul li Link{
   color: black;
   text-decoration: none;
}
footer { 
  margin-top: 2rem;
  background: black;
  padding: 3rem 0rem;
  color: white;
  width: 100%;
}

form{
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin: 3rem auto;
  width: 60%;
}
form .mb-3{
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.marginh1{
  margin-top: 2rem;
}
.productlist{
  justify-content: center;
  display: flex; 
  align-items: center;
  margin: 2rem;
} 
.box{
  justify-content: center;
  display: flex; 
  align-items: center; 
}
.productlist li{
  list-style: none; 
  padding: 1rem 3rem;
  color: blue;
} 
.box li{
  list-style: none; 
  padding: 1rem 3rem;
} 
.errormessage{
  color: red;
  align-self: self-start;
  font-size: 1.5rem;
}
.dropdown-menu .dropdown-item:hover{
  background: black;
}
a{
  text-decoration: none;
  color: black;
}
.categoryfilter{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: .7rem;
}
.cart{
  display: flex;
  align-items: center;
}